import React, { useState, useRef } from "react";
import styled from "styled-components";

const BlogAddBlock = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    input {
        width: 100%;
        height: 50px;
        padding: 0px 10px;
        background: #5bbe9d;
        color: white;
        font-size: 18px;
        font-weight: bold;
        box-sizing: border-box;
        outline: none;
    }
    input::placeholder {
        color: #eee;
    }
    button {
        height: 50px;
        background: #2a828b;
        padding: 0px 20px;
        color: white;
        font-weight: bold;
        box-sizing: border-box;
    }
`;

const BlogAdd = ({ list, setList }) => {
    const [input, setInput] = useState("");
    const conInput = useRef();
    const nextId = list.length;
    const onLoad = () => {
        setList([
            ...list,
            {
                id: nextId + 1,
                content: input,
            },
        ]);
        setInput("");
        conInput.current.focus();
    };
    return (
        <BlogAddBlock>
            <input
                placeholder=" "
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyPress={(e) => (e.key === "Enter" ? onLoad() : null)}
                ref={conInput}
            />
            <button onClick={onLoad}>UpLoad</button>
        </BlogAddBlock>
    );
};

export default BlogAdd;
