import React from "react";
import styled from "styled-components";

const NewsListBlock = styled.div`
    & > div {
        display: flex;
        align-items: flex-start;
        gap: 20px;
        div {
            h2 {
                font-size: 18px;
                font-weight: bold;
            }
            span {
                max-width: 500px;
                margin-top: 15px;
                font-size: 15px;
                line-height: 23px;
                color: gray;
            }
        }
    }
    img {
        width: 300px;
        height: 200px;
        object-fit: cover;
    }
    & + & {
        margin-top: 20px;
    }
`;

const NewsList = ({ article }) => {
    const { title, description, url, urlToImage } = article;

    return (
        <NewsListBlock>
            <div>
                <a href={url}>
                    <img src={urlToImage} alt={title} />
                </a>
                <div>
                    <h2>{title}</h2>
                    <span className="ellipsis">{description}</span>
                </div>
            </div>
        </NewsListBlock>
    );
};

export default NewsList;
