import React, { useState, useEffect, useCallback } from "react";
import styled from "styled-components";
import axios from "axios";
import NewsList from "./NewsList";
import NewsCategories from "./NewsCategories";

const NewsListTempleteBlock = styled.div`
    width: 100%;
    max-width: 1280px;
    margin: 10px auto;
    padding: 0 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
`;

const NewsListTemplete = () => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);
    const [cate, setCate] = useState("all");
    const onSelect = useCallback((cate) => setCate(cate), []);

    useEffect(() => {
        const fetchData = async () => {
            setLoading(true);
            try {
                const query = cate === "all" ? "" : `&category=${cate}`;
                const response = await axios.get(
                    `https://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=a53870dfee0a4c5c9a15095e89737382`,
                );
                setArticles(response.data.articles);
            } catch (e) {
                console.log(e);
            }
            setLoading(false);
        };
        fetchData();
    }, [cate]);

    if (loading) {
        return <NewsListTempleteBlock>...</NewsListTempleteBlock>;
    }

    if (!articles) {
        return null;
    }

    return (
        <NewsListTempleteBlock>
            <NewsCategories cate={cate} onSelect={onSelect} />
            {articles.map((article) => (
                <NewsList key={article.url} article={article} />
            ))}
        </NewsListTempleteBlock>
    );
};

export default NewsListTemplete;
