import React, { useState } from "react";
import styled from "styled-components";
import BlogAdd from "./BlogAdd";
import BlogList from "./BlogList";

const BlogListBlock = styled.div`
    width: 100%;
    & > .title {
        width: 100%;
        padding: 20px;
        font-size: 20px;
        font-weight: bold;
        color: white;
        border-radius: 20px 20px 0 0;
        background: #38a098;
        box-sizing: border-box;
    }
`;

const BlogListWrap = () => {
    const [list, setList] = useState([
        {
            id: 1,
            content: "React",
        },
        {
            id: 2,
            content: "HTML",
        },
        {
            id: 3,
            content: "CSS",
        },
        {
            id: 4,
            content: "JAVASCRIPT",
        },
    ]);
    return (
        <BlogListBlock>
            <div className="title">BLOG</div>
            <BlogAdd list={list} setList={setList} />
            <BlogList list={list} setList={setList} />
        </BlogListBlock>
    );
};

export default BlogListWrap;
