import React from "react";
import styled, { css } from "styled-components";

const categories = [
    {
        name: "all",
        text: "",
    },
    {
        name: "business",
        text: "",
    },
    {
        name: "entertainment",
        text: "",
    },
    {
        name: "health",
        text: "",
    },
    {
        name: "science",
        text: "",
    },
    {
        name: "sports",
        text: "",
    },
    {
        name: "technology",
        text: "",
    },
];

const NewsCategoriesBlock = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 20px;
    gap: 20px;
    border-bottom: 1px solid #5bbe9d;
    border-top: 1px solid #5bbe9d;
    box-sizing: border-box;
    margin-bottom: 10px;
`;
const Category = styled.div`
    padding: 15px 0;
    font-size: 18px;
    color: #8c8eb1;
    font-weight: bold;
    cursor: pointer;
    &:hover {
        color: #2a828b;
    }

    ${(props) =>
        props.active &&
        css`
            color: #2a828b;
        `}
`;

const NewsCategories = ({ cate, onSelect }) => {
    return (
        <NewsCategoriesBlock>
            {categories.map((category) => (
                <Category
                    key={category.name}
                    active={cate === category.name}
                    onClick={() => onSelect(category.name)}
                >
                    {category.text}
                </Category>
            ))}
        </NewsCategoriesBlock>
    );
};

export default NewsCategories;
