import React from "react";
import styled from "styled-components";

const BlogListBlock = styled.div`
    width: 100%;
    padding: 15px 10px;
    border-left: 1px solid #5bbe9d;
    border-right: 1px solid #5bbe9d;
    border-bottom: 1px solid #5bbe9d;
    border-radius: 0px 0px 5px 5px;
    box-sizing: border-box;
    ul {
        li {
            display: flex;
            align-items: center;
            justify-content: space-between;
            div {
                display: flex;
                align-items: baseline;
                gap: 10px;
                strong {
                    font-size: 15px;
                    line-height: 1;
                }
                span {
                    font-size: 14px;
                    word-break: break-all;
                }
            }
            .del {
                padding: 8px 14px;
                font-weight: bold;
                background: #8c8eb1;
                color: white;
                border-radius: 5px;
            }
            &:not(:first-child) {
                margin-top: 10px;
            }
        }
    }
`;

const BlogList = ({ list, setList }) => {
    return (
        <BlogListBlock>
            <ul>
                {list.map((item) => (
                    <li key={item.id}>
                        <div>
                            <strong>{item.id}</strong>
                            <span className="ellipsis">{item.content}</span>
                        </div>
                        <button
                            className="del"
                            onClick={() => {
                                setList(
                                    list.filter((del) => item.id !== del.id),
                                );
                            }}
                        >
                            
                        </button>
                    </li>
                ))}
            </ul>
        </BlogListBlock>
    );
};

export default BlogList;
